import { NgModule } from '@angular/core';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { AddAddressComponent } from './add-address.component';
import { Router, RouterModule } from '@angular/router';
import { PageHeaderModule } from '../../shared';
import { AddAddressRoutingModule } from './add-address-routing.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatStepperModule, MatFormFieldModule, MatCardModule, MatInputModule, MatSelectModule, MatButtonModule } from '@angular/material';

@NgModule({
  imports: [ AddAddressRoutingModule,
    FormsModule,
    ReactiveFormsModule, PageHeaderModule,
    MatStepperModule,
    MatFormFieldModule,
    MatCardModule,
    MatInputModule,
    MatSelectModule,
    MatButtonModule,
      
      NgbModule],
  declarations: [AddAddressComponent]
})
export class AddAddressModule { }










